"use client";
import { useSession } from "next-auth/react";
import { useState } from "react";
import { FaUserCircle } from "react-icons/fa";

export default function AccountSection() {
  const { data: session } = useSession();
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [msg, setMsg] = useState(null);

  const submit = async (e) => {
    e.preventDefault();
    const res = await fetch("/api/auth/change-password", {
      method:"POST",
      headers:{"Content-Type":"application/json"},
      body: JSON.stringify({ currentPassword, newPassword }),
    });
    const { ok, error } = await res.json();
    setMsg(ok ? "Contrase√±a actualizada." : error);
  };

  return (
    <div style={{ maxWidth: 400, margin: "auto" }}>
      <h3><FaUserCircle className="me-2"/> Mi cuenta</h3>
      <p><strong>Nombre:</strong> {session.user.name}</p>
      <p><strong>Email:</strong> {session.user.email}</p>

      <hr />

      <h5>Cambiar contrase√±a</h5>
      <form onSubmit={submit}>
        <div className="form-floating mb-2">
          <input
            type="password"
            className="form-control"
            id="currentPassword"
            placeholder="Actual"
            value={currentPassword}
            onChange={e=>setCurrentPassword(e.target.value)}
            required
          />
          <label htmlFor="currentPassword">Contrase√±a actual</label>
        </div>
        <div className="form-floating mb-3">
          <input
            type="password"
            className="form-control"
            id="newPassword"
            placeholder="Nueva"
            value={newPassword}
            onChange={e=>setNewPassword(e.target.value)}
            required
          />
          <label htmlFor="newPassword">Contrase√±a nueva</label>
        </div>
        <button className="btn btn-orange w-100">Actualizar</button>
      </form>
      {msg && <div className="alert alert-info mt-3">{msg}</div>}
    </div>
  );
}
