"use client";
import { useState, useEffect } from "react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { format } from "date-fns";

const servicios = [
  "Estimulaci√≥n en agua","Estimulaci√≥n en piso","Quiropr√°ctica","Fisioterapia",
  "Masajes","Cosmetolog√≠a","Prevenci√≥n de lesiones","Preparaci√≥n f√≠sica",
  "Nutrici√≥n","Medicina en rehabilitaci√≥n"
];
const terapeutas = [
  "Jes√∫s Ram√≠rez","Miguel Ram√≠rez","Alitzel Pacheco","Francia","Gisela"
];
const SLOT_START = 10;
const SLOT_END = 19;

export default function ReservasForm() {
  const [servicio, setServicio] = useState("");
  const [terapeuta, setTerapeuta] = useState("");
  const [fecha, setFecha] = useState(new Date());
  const [takenSlots, setTakenSlots] = useState<number[]>([]);
  const [slot, setSlot] = useState<number|null>(null);

  useEffect(()=>{
    if (terapeuta && fecha) {
      const d = format(fecha, "yyyy-MM-dd");
      fetch(`/api/appointments/slots?date=${d}&therapistId=${terapeuta}`)
        .then(r=>r.json())
        .then(setTakenSlots);
    }
  },[terapeuta,fecha]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const date = new Date(fecha);
    date.setHours(slot,0,0,0);
    // aqu√≠ env√≠as a tu API y luego a Stripe‚Ä¶
    console.log({ servicio, terapeuta, date });
  };

  return (
    <form onSubmit={handleSubmit} className="mb-4" style={{ maxWidth: 600, margin: "auto" }}>
      <h3 className="text-center mb-3">Reservar cita</h3>
      <div className="mb-3">
        <label>Servicio</label>
        <select className="form-select" value={servicio} onChange={e=>setServicio(e.target.value)} required>
          <option value="">Selecciona servicio</option>
          {servicios.map(s=><option key={s} value={s}>{s}</option>)}
        </select>
      </div>
      <div className="mb-3">
        <label>Terapeuta</label>
        <select className="form-select" value={terapeuta} onChange={e=>setTerapeuta(e.target.value)} required>
          <option value="">Selecciona terapeuta</option>
          {terapeutas.map(t=><option key={t} value={t}>{t}</option>)}
        </select>
      </div>
      <div className="mb-3">
        <label>Fecha</label>
        <Calendar
          onChange={setFecha}
          value={fecha}
          minDate={new Date()}
          className="mb-3"
        />
      </div>
      <div className="mb-3">
        <label>Horario</label>
        <div>
          {Array.from({ length: SLOT_END - SLOT_START }, (_,i)=>SLOT_START + i).map(h => {
            const disabled = takenSlots.includes(h);
            return (
              <button
                type="button"
                key={h}
                className={`btn slot-btn ${slot===h?"selected":""}`}
                disabled={disabled}
                onClick={()=>setSlot(h)}
              >
                {h}:00
              </button>
            );
          })}
        </div>
      </div>
      <button type="submit" className="btn btn-primary w-100" disabled={!slot}>
        Confirmar y pagar
      </button>
    </form>
  );
}
