// src/components/Navbar.jsx
"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import Image from "next/image";
import { useSession, signOut } from "next-auth/react";
import {
  Navbar as BSNavbar,
  Nav,
  Container,
  NavDropdown,
} from "react-bootstrap";
import {
  FaInfoCircle,
  FaCalendarAlt,
  FaServicestack,
  FaSignInAlt,
  FaUser,
} from "react-icons/fa";

export default function Navbar() {
  const [scrolled, setScrolled] = useState(false);
  const { data: session } = useSession();

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 50);
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <BSNavbar
      sticky="top"
      expand="lg"
      className={`py-3 ${scrolled ? "navbar-scrolled" : "navbar-base"}`}
    >
      <Container>
        <Link href="/" className="navbar-brand d-flex align-items-center gap-2">
          <Image
            src="/images/logo_bloom_clean.png"
            alt="Bloom"
            width={105}
            height={105}
            className="logo-img"
            priority
          />
        </Link>

        <BSNavbar.Toggle aria-controls="navbar-nav" />
        <BSNavbar.Collapse id="navbar-nav">
          <Nav className="ms-auto gap-3 align-items-center">
            <Link href="/nosotros" className="nav-link d-flex align-items-center">
              <FaInfoCircle className="me-1" /> Nosotros
            </Link>

            {/* Antes apuntaba a /servicios/agua */}
            <Link href="/reservas" className="nav-link d-flex align-items-center">
              <FaCalendarAlt className="me-1" /> Agendar
            </Link>

            {!session && (
              <Link href="/login" className="nav-link d-flex align-items-center">
                <FaSignInAlt className="me-1" /> Iniciar sesi√≥n
              </Link>
            )}

            {session && (
              <NavDropdown
                title={
                  <span className="d-flex align-items-center">
                    <FaUser className="me-1" /> {session.user.name}
                  </span>
                }
                id="user-dd"
                align="end"
                menuVariant="light"
                className="nav-link"
              >
                <Link href="/dashboard" className="dropdown-item">
                  Mi dashboard
                </Link>
                <button
                  className="dropdown-item"
                  onClick={() => signOut({ callbackUrl: "/" })}
                >
                  Cerrar sesi√≥n
                </button>
              </NavDropdown>
            )}
          </Nav>
        </BSNavbar.Collapse>
      </Container>
    </BSNavbar>
  );
}
